import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { isThisMonth } from 'date-fns';
import { WizardEvent, WizardEventType } from '../model/wizard.model';

@Injectable({
    providedIn: 'root'
})
export class WizardService {
    private wizardData:Subject<any> = new Subject();
    //ç¨äºå­å¨å½åæ¾ç¤ºpageid
    currentPageId:string;
    // ä¿å­æ°æ®
    private wizardPagesData = {};
    //æ¯å¦å¹³éº
    private pageFill = true;
    /**
     * å¯¹è±¡ç»æ
     *  {
     *      "activeIndex":-1,
             "stepMessages":[
                {
                    "id":"deliverytime",
                    "title":"è®¾ç½®åè´§æ¶é´",
                    "icon":""
                },....]
        }
     */
    private onlyProgressData = null;
    // æ­¥éª¤æ¡æ°æ®ä¿æ
    private progressData: Subject<any> = new Subject();
    // å½ååå¯¼ç¶æ
    private wizardState: Subject<any> = new Subject();
    // ä¿®æ¹åå¯¼æ­¥éª¤
    changeActiveIndex: Subject<number> = new Subject();

    constructor() {
        this.progressData.subscribe(data => {
            this.onlyProgressData = data;
        });
    }
    //å­å¨é¡µé¢æ¯å¦å¹³éº
    updatePageFill(fill:boolean){
        this.pageFill = fill;
    }
    getPageFill(){
        return this.pageFill;
    }
    /**
     * æ´æ°åå¯¼ç¶æ
     * state: Finish,Cancel,Prev,Next
     */
    updateWizardState(state: WizardEventType, curPageId: string, stepToPageId?:string) {
        let toPageId = '';
        switch (state) {
            case WizardEventType.Prev:
                toPageId = this.getPrePageId(curPageId);
                break;
            case WizardEventType.Next:
                toPageId = this.getNextPageId(curPageId);
                break;
            case WizardEventType.StepChange:
                toPageId = stepToPageId ? stepToPageId: curPageId
                break;
            case WizardEventType.ChangeIndex:
                toPageId = stepToPageId !== '' ? stepToPageId: curPageId
                break
            default:
                toPageId = curPageId;
        }
        // åªæé¡µé¢ååæ¶åï¼ææ¹å
        if (!toPageId) {
            return;
        }
        this.currentPageId = toPageId;
        this.wizardState.next(new WizardEvent(state, curPageId, toPageId));
    }
    /**
     * è·ååå¯¼ç¶æåå
     */
    getWizardState() {
        return this.wizardState;
    }
    /**
     * è·åæ´ä¸ªåå¯¼ç¨çæ°æ®
     * åºæ¯ï¼åå¯¼æ´ä½æ°æ®ï¼å¨ä¸åé¡µä¹é´éç¨
     */
    getWizardData(){
        return this.wizardData;
    }
    /**
     * è®¾ç½®åå¯¼ç¨çæ°æ®
     * åºæ¯ï¼åå¯¼æ´ä½æ°æ®ï¼å¨ä¸åé¡µä¹é´éç¨
     */
    setWizardData(data){
        this.wizardData.next(data);
    }
    /**
     * è®¾ç½®æé¡µé¢æ°æ®
     * @param pageid
     * @param data
     */
    setPageData(data,pageid?: string) {
        let id = pageid? pageid : this.currentPageId;
        this.wizardPagesData[id] = data;
    }
    /**
     * è·åæé¡µé¢æ°æ®
     * @param pageid
     */
    getPageData(pageid) {
        if (this.wizardPagesData.hasOwnProperty(pageid)) {
            return this.wizardPagesData[pageid];
        }
        return {};
    }
    // /**
    //  * åºæ¯ï¼Wizardç»ä»¶åå§åæ­¥éª¤æ¡æ°æ®
    //  * @param data
    //  */
    // initProgressData(data) {
    //     this.progressData.next(data);
    // }

    /**
     * ç®çï¼æ´æ°æ­¥éª¤æ¡æ°æ®
     * åºæ¯ï¼PageDetailç»ä»¶ï¼éè¦æ´æ°æ­¥éª¤æ°æ®,å·æ°æ­¥éª¤æ¡çé¢
     */
    updateProgressData(data) {
        this.progressData.next(data);
    }
    /**
     * ç®çï¼è·åæ­¥éª¤æ¡æ°æ®
     * åºæ¯ï¼PageDetailç»ä»¶,æ¥çæ­¥éª¤æ¡æ°æ®
     */
    getProgressData() {
        return this.progressData;
    }

    /**å­å¨currentpageid */
    updateCurrentPageId(id:string){
        this.currentPageId = id ;
    }
    getCurrentPageId(){
        return this.currentPageId;
    }

    /**
     * è·åä¸ä¸é¡µpageid
     * @param pageid
     */
    getPrePageId(pageid?: string): string {
        let id:string;
        id = pageid?pageid:this.currentPageId;
        const pageIdIndex = this.getIndexByPageId(id);
        if (pageIdIndex <= 0) {
            return '';
        }
        let prePage = this.onlyProgressData.stepMessages[pageIdIndex - 1];
        return prePage['id'];
    }

    /**
     * è·åä¸ä¸é¡µ
     * @param pageid
     */
    getNextPageId(pageid): string {
        const pagelen = this.onlyProgressData.stepMessages.length;
        const pageIdIndex = this.getIndexByPageId(pageid);
        if (pageIdIndex == pagelen - 1) {
            return '';
        }
        let nextPage = this.onlyProgressData.stepMessages[pageIdIndex + 1];
        return nextPage['id'];
    }
    /**
     * æ¯å¦æ¯æåä¸é¡µ
     * @param pageid
     */
    isLastPage(pageid): boolean {
        const pagelen = this.onlyProgressData.length;
        let lastPage = this.onlyProgressData.stepMessages[pagelen - 1];
        return lastPage['id'] == pageid;
    }

    /**
     * æ¯å¦æ¯å½åé¡µ
     * @param pageid
     */
    isCurrentPage(pageid): boolean {
        const activeIndex = this.onlyProgressData.activeIndex;
        if (activeIndex < 0) {
            return false;
        }
        let curPage = this.onlyProgressData.stepMessages[activeIndex];
        return curPage['id'] == pageid;
    }
    /**
     * æ¯å¦æ¯åå§é¡µ
     * @param pageid
     */
    isFirstPage(pageid): boolean {
        if (this.onlyProgressData.activeIndex < 0) {
            return false;
        }
        return this.onlyProgressData.stepMessages[0]['id'] == pageid;
    }
    /**
     * æ ¹æ®IDè·åindex
     * @param pageid 
     */
    getIndexByPageId(pageid): number {
        if(this.onlyProgressData&&this.onlyProgressData.hasOwnProperty('stepMessages')){
            let pageIdIndex = this.onlyProgressData.stepMessages.findIndex(item => {
                if (item['id'] == pageid) {
                    return true;
                }
                return false;
            });
            return pageIdIndex;
        }
    }


    //20190211 æå¡åæ°å¢äºä»¶
    /**
     * ä¸ä¸æ­¥
     */
    nextStep() {
        this.updateWizardState(WizardEventType.Next, this.currentPageId);
    }
    /**
     * ä¸ä¸æ­¥
     */
    prevStep() {
        this.updateWizardState(WizardEventType.Prev, this.currentPageId);
    }
    /**
     * ç»æåå¯¼
     */
    finishWizard() {
        this.updateWizardState(WizardEventType.Finish, this.currentPageId);
    }
    /**
     * åæ¶åå¯¼
     */
    cancelWizard() {
        this.updateWizardState(WizardEventType.Cancel, this.currentPageId);
    }

    /**
     * ä¿®æ¹åå¯¼æ¾ç¤ºæ­¥éª¤
     * @param num ç´¢å¼ 0å¼å§
     */
    setActiveIndex(num:number){
        let toPageId;
        if(this.onlyProgressData&&this.onlyProgressData.hasOwnProperty('stepMessages')){
            let activeNum:number  = (num>= 0 && num < this.onlyProgressData.stepMessages.length)? num: 0
            toPageId = this.onlyProgressData.stepMessages[activeNum]['id'];
        }
        this.updateWizardState(WizardEventType.ChangeIndex, this.currentPageId, toPageId);
        this.changeActiveIndex.next(num);
    }
}
