import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable()
export class PageService {
    activeState: Subject<any> = new Subject();
    
    private tPageId = '';
    constructor() {}
    setPageId(value) {
        this.tPageId = value;
    }
    getPageId() {
        return this.tPageId;
    }
}