export enum WizardEventType {
    Finish,
    Cancel,
    Prev,
    Next,
    StepChange,
    ChangeIndex
}
export class WizardEvent {
    state: WizardEventType; // Pre
    fromPageId: string;
    toPageId: string;
    constructor(state, fromPageId, toPageId) {
        this.state = state;
        this.fromPageId = fromPageId;
        this.toPageId = toPageId;
    }
}
